.flashcardContainer {
  perspective: 1000px;
  width: 100%;
  max-width: 60rem;
  padding: 0 1rem; /* Add padding for small screens */
  margin: 0 auto; /* Center align the container */
}

.flashcard {
  width: 60rem; /* Original width */
  height: 15rem; /* Original height */
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.6s;
  cursor: pointer;
  border-radius: 1rem;
}

.flashcard.flip {
  transform: rotateY(180deg);
}

.flashcardFront,
.flashcardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
}

.flashcardFront {
  background: linear-gradient(145deg, #f9d423, #ff4e50);
}

.flashcardBack {
  background: linear-gradient(145deg, #1e88e5, #43a047);
  transform: rotateY(180deg);
}

.flashcardText {
  font-size: 1.5rem;
  font-weight: 500;
  text-align: center;
  padding: 1rem;
  color: #333;
}

/* Responsive Styles */
@media (max-width: 1200px) {
  .flashcard {
    width: 50rem; /* Reduce width for larger tablets and small desktops */
    height: 12.5rem; /* Scale down height proportionally */
  }

  .flashcardText {
    font-size: 1.4rem; /* Slightly reduce text size */
  }
}

@media (max-width: 992px) {
  .flashcard {
    width: 40rem; /* Reduce width for tablets */
    height: 10rem; /* Further reduce height */
  }

  .flashcardText {
    font-size: 1.2rem; /* Reduce text size accordingly */
  }
}

@media (max-width: 768px) {
  .flashcard {
    width: 30rem; /* Reduce width for smaller tablets */
    height: 9rem; /* Reduce height accordingly */
  }

  .flashcardText {
    font-size: 1.1rem; /* Adjust text size for smaller screens */
  }
}

@media (max-width: 576px) {
  .flashcard {
    width: 20rem; /* Reduce width for mobile devices */
    height: 8rem; /* Adjust height for mobile */
  }

  .flashcardText {
    font-size: 1rem; /* Reduce text size for mobile */
  }
}
