/* Flashcard.module.css */

.flashcardContainer {
    perspective: 1000px;
  }
  
  .flashcard {
    width: 24rem; /* width-96 */
    height: 16rem; /* h-64 */
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.6s;
    cursor: pointer;
  }
  
  .flip {
    transform: rotateY(180deg);
  }
  
  .flashcardFront,
  .flashcardBack {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
  }
  
  .flashcardBack {
    transform: rotateY(180deg);
  }
  